import React from 'react';
import classNames from 'classnames/bind';
import { FooterProps } from 'core/types/modals';

import styles from './styles.scss';

const cn = classNames.bind(styles);

const Footer: React.FC<FooterProps> = ({
  footerItemsLeft,
  footerItemsRight,
  size = 'default',
  dataTestId = 'footer_id'
}) => {
  const isFooterLeft = footerItemsLeft?.length;
  const isFooterRight = footerItemsRight?.length;

  if (!isFooterLeft && !isFooterRight) {
    return null;
  }

  const footerClassName = cn(
    'footer',
    `footer--${ size }`,
    {
      'footer--left': isFooterLeft,
      'footer--right': isFooterRight,
      'footer--two_blocks': isFooterLeft && isFooterRight
    }
  );

  const leftSectionClassName = cn(
    'left_section',
    { 'left_section--with_space': isFooterLeft }
  );

  return (
    <div className={ footerClassName } data-test-id={ dataTestId }>
      { isFooterLeft && (
        <div className={ leftSectionClassName }>
          {
            // eslint-disable-next-line react/no-array-index-key
            footerItemsLeft?.map((item, index) => <React.Fragment key={ index.toString() }>{ item }</React.Fragment>)
          }
        </div>
      ) }
      { isFooterRight && (
        <div className={ styles.right_section }>
          {
            // eslint-disable-next-line react/no-array-index-key
            footerItemsRight?.map((item, index) => <React.Fragment key={ index.toString() }>{ item }</React.Fragment>)
          }
        </div>
      ) }
    </div>
  );
};

export default Footer;
